.icon {
  display: block;
  height: 24px;
  width: 24px;
  background-repeat: no-repeat;

  @include hover(transform, scale(1.2));

  @include on-tablet {
    height: 32px;
    width: 32px;
    position: relative;
  }

  &--phone {
    background-image: url(../../images/icons/icon-phone.png);

  @include on-desktop {
  &::before {
  content: '+1 234 5555-55-55';
  display: none;
  position: absolute;
  left: -147px;
  top: 25%;
  color: $main-text-color;
  font-weight: 700;
  height: 16px;
  line-height: 16px;
  font-size: 12px;
  text-align: center;
  letter-spacing: 2px;
  white-space: nowrap;
  }

  &:hover::before {
  display: block;
  }
}
}

  &--menu {
  background-image: url(../../images/icons/icon-burger-menu.png);

  &:hover {
  transform: scale(1.2);
  background-image: url(/images/icons/icon-burger-menu-hover.png);
  }
}

  &--close {
  background-image: url(../../images/icons/icon-close.png);
  }
}
